body {
  font-family: Arial, sans-serif;
  margin: 20px;
}

.container {
  max-width: 900px;
  margin: auto;
}

.flex-row {
  display: flex;
  justify-content: space-between;
}

.flex-large {
  flex: 0 0 45%;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
}

button {
  margin-right: 5px;
  cursor: pointer;
}
